export default {
  rem: function pl() {
//
//        w
    var w = document.documentElement.offsetWidth;
    // console.log(w);
    if(320<w&&w<562){
//        wpxhtmlfont-size
      document.documentElement.style.fontSize = w/375 * 100+ "px";
      console.log("rem",w/375 * 100,"px");
    }else if(w>=562){
      document.documentElement.style.fontSize = 150+'px';
      console.log("rem150px");
    }else if(w<=320){
      document.documentElement.style.fontSize = 86+'px';
      console.log("rem86px");
    } else {
      console.log(444);
    }
  }
}

