

// import { falseHotWord, falseOrder, falseBussinessbrief, falseBussinessInfo } from '../../api/data.js';

export default {

  state:{
    //bool
    show:true,
    //
    count:1,
    // // 
    // falseHotWord,
    // // 
    // falseOrder,
    // // 
    // falseBussinessbrief,
    // // 
    // falseBussinessInfo
  },

  getters:{
    not_show(state){ //statestate
      return !state.show;
    }
  },

  mutations:{
    switch_dialog(state){ //statestate
      state.show = state.show ? false : true;
      console.log(1)
      //state
    },
    conent(state){
      state.count++;
      console.log(state.count);
    }
  },

  actions:{
    switch_dialog(context){  //context$store
      context.commit('switch_dialog');
      //mutations
    },
    incrementAsync ({ commit }) {  //   action 
      setInterval(() => {
        commit('conent');
      }, 1000)
    }
  },

}



